
.container {
  margin-top: 40px;
  margin-bottom: 40px;
}
.header {
  margin-bottom: 40px;
}
.form-control {
  border-radius: 0px;
}
#signature {
  border: 1px solid #d6d6d6;
}
.btn {
  background-color: #002997;
  border-radius: 0px;
  color: white;
  margin-top: 20px;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  cursor: pointer;
}
.btn:hover {
    background-color: #00227e;
}
h5 {
  margin-bottom: 1rem;
}
.form-wrapper {
  background-color: #F2F2F2;
  padding: 30px;
}
.section-break {
  margin-top: 50px;
}
.signature-renderer {
  padding: 10px;
}



